.list-link
{
    text-decoration: none;
    cursor: pointer;
    color: #FBA94C;
    padding: 8px;
    border-radius: 8px;
    transition: 500ms;
    border: 1px solid transparent;
}
.list-link:hover {
    background-color: #2E1B06;
    border-color: #FBA94C;
}
.body-blink {

    animation: blink 400ms ease-out;
}
@keyframes blink {
    0% {
        background-color: #505059;
    }
    100% {
        background-color: #323238;
    }
}